import { Injectable } from  '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';
import {Observable} from "rxjs";

@Injectable()
export class RestClient {
  constructor (private _http: Http){ }

  URL = "http://" + window.location.host + "/Turnierverwaltung/rs/";

  getData(){
    return this._http.get(this.URL+"team").map(res => res.json());
  }

  getMatchesForTeam(id:number){
    return this._http.get(this.URL+"match/by?teid="+id).map(res => res.json());
  }

  getTournament(id: number)
  {
    return this._http.get(this.URL+"tournament/"+id).map(res => res.json());
  }

  getTournamentDTOs()
  {
    console.log(this.URL);
    return this._http.get(this.URL+"tournament").map(res => res.json());
  }

  getMatchesInTournament(id:number)
  {
    return this._http.get(this.URL+"match/by?toid="+id).map(res => res.json());
  }

  /*getMatchesInTournamentWithInterval(id:number)
  {
    return Observable.interval(2000)
            .switchMap(() =>{this._http.get("http://vm15.htl-leonding.ac.at:8090/Turnierverwaltung/rs/match/by?toid="+id).map(res => res.json())});
  }*/

  postJson(json: string){
    return JSON.stringify(json);
  }

  getRounds(id:number)
  {
    return this._http.get(this.URL+"round/by?toid="+id).map(res => res.json());
  }

  getMatchesByRo(id:number)
  {
    return this._http.get(this.URL+"match/by?roid="+id).map(res => res.json());
  }

  getMatchesByTo(id:number){
    return this._http.get(this.URL+"match/by?toid="+id).map(res => res.json());
  }
}



