import {Component, OnInit, EventEmitter, Output, Input} from '@angular/core';
import {MatchInDepth} from "../entities/matchInDepth";
import {Match} from "../entities/match";
import {RestClient} from "../rest.client";
import {Router} from '@angular/router';


@Component({
  selector: 'app-currenttournament',
  templateUrl: './currenttournament.component.html',
  styleUrls: ['./currenttournament.component.css']
})
export class CurrenttournamentComponent implements OnInit {

  matchesDone: Match[]=[];
  matchesToBeDone: MatchInDepth[]=[];

  public location = '' ;

  @Input() private showCurrentTournament: boolean=false;
  @Output() showCurrentTournamentChanged= new EventEmitter<boolean>();

  constructor(private repo: RestClient, private  _router : Router) {
    this.location = _router.url;
  }

  ngOnInit() {
    var id=JSON.parse(localStorage.getItem('currentTournament'));
    if(id!=null){
      this.getMatches(id);
    }
  }


  getMatches(id:number){
    this.repo.getMatchesInTournament(id).subscribe(res =>{
      res.forEach(match=>{
        if(match.active) {
          var team1;
          var team2;

          if(match.team1==null) {
            team1="Wildcard";
            team2=match.team2.name;
          }else if(match.team2==null)
          {
            team1=match.team1.name;
            team2="Wildcard";
          }else{
            team1=match.team1.name;
            team2=match.team2.name;
          }
          this.matchesToBeDone.push(new MatchInDepth(match.id, team1, team2, match.result, match.startTime, match.court));
        }else{
          var team1;
          var team2;

          if(match.team1==null) {
            team1="Wildcard";
            team2=match.team2.name;
          }else if(match.team2==null)
          {
            team1=match.team1.name;
            team2="Wildcard";
          }else{
            team1=match.team1.name;
            team2=match.team2.name;
          }
          this.matchesDone.push(new Match(match.id, team1, team2,match.result));
        }
      });
    });
  }
}



